import React, { useMemo, useState, ReactNode, Ref } from 'react';
import classNames from 'classnames';
import moment from 'moment';
import { compact, flatMap, flatten, Many } from 'lodash';
import { SectionContext } from './SectionContext';
import { useEnvTested } from '../../../../hooks/useEnvTested';


const now = moment.utc();

const joinClassName = (...className: (string | undefined)[]) => compact(className).join('-');

const useSectionPartClassName = (className?: string, parts?: string | string[]) => useMemo(
    () => classNames(
        ...flatMap(
            flatten([parts] as ArrayLike<Many<string>>),
            part => [
                joinClassName('cv-pdf-section', part),
                joinClassName(className, part)
            ]
        )
    ),
    [parts, className]
);

const attrs = (
    <>
        © 2020 <br />
        <a href="mailto:kudla.artem@gmail.com">kudla.artem@gmail.com</a> <br />
        <a href="https://kudla.github.io/cv">https://kudla.github.io/cv</a>
    </>
);

const {userAgent} = navigator;

export const Section = ({children, className }: {children?: ReactNode, className?: string}) => {
    const componentClassName = useSectionPartClassName(className);
    const headerClassName = useSectionPartClassName(className, 'header');
    const headerClientClassName = useSectionPartClassName(className, 'header-client');
    const bodyClassName = useSectionPartClassName(className, 'body');
    const footerClassName = useSectionPartClassName(className, 'footer');
    const footerClientPlaceholderClassName = useSectionPartClassName(className, ['footer-client', 'footer-client-placeholder']);
    const footerClientStaticClassName = useSectionPartClassName(className, ['footer-client', 'footer-client-static']);


    const [header, mountHeader] = useState();
    const [footerPlaceholder, mountFooterPlaceholder] = useState();
    const [footerStatic, mountFooterStatic] = useState();

    const sectionContext = useMemo(
        () => ({
            header,
            footerPlaceholder,
            footerStatic
        }),
        [header, footerPlaceholder, footerStatic]
    );

    const envTested = useEnvTested(userAgent);

    return (
        <SectionContext.Provider value={sectionContext}>
            <table className={componentClassName}>
                <thead
                    className={headerClassName}
                >
                    <tr>
                        <th
                            className={headerClientClassName}
                        >
                            <h2
                                className="cv-pdf-section-header-title"
                                ref={mountHeader as any as Ref<HTMLTableHeaderCellElement>}
                            />

                            <a href="https://kudla.github.io/cv">
                                <img className="cv-pdf-qr" src="images/qr.png" />
                            </a>

                            {
                                !envTested
                                && (
                                    <div className="cv-pdf-untested-env">
                                        Sorry. The application was not tested on your environment yet.
                                    </div>
                                )
                            }

                        </th>
                    </tr>
                </thead>

                <tbody className={bodyClassName}>
                    {children}
                </tbody>

                <tfoot
                    className={footerClassName}
                >
                    <tr>
                        <th
                            className={footerClientPlaceholderClassName}
                            ref={mountFooterPlaceholder  as any as Ref<HTMLTableHeaderCellElement>}
                        >
                            {attrs}
                        </th>
                    </tr>
                    <tr>
                        <th
                            className={footerClientStaticClassName}
                            ref={mountFooterStatic  as any as Ref<HTMLTableHeaderCellElement>}
                        >
                            {attrs}
                        </th>
                    </tr>
                </tfoot>
            </table>
        </SectionContext.Provider>
    )
};
