import React, { ReactNode } from 'react';

import { Section, HeaderTitle, Footer, Row, Cell } from '../Document';
import { Tag } from '../Tag';

const Thumbnail = ({title, children, link}: {
    title: string,
    children: ReactNode,
    link?: string
}) => {
    return (
        <li className="cv-pdf-thumbnail">
            <Tag
                mass={3}
                elevation={3}
                to={link}
            >
                {
                    children
                }
            </Tag>
            <a href={link}>
                {title}
            </a>
        </li>
    )
};

const Book = ({picture}: {
    picture: string
}) => (
    <img className="cv-pdf-book" src={`images/books/${picture}`} />
);

const Gallery = ({children}: {
    children: ReactNode
}) => (
    <ul className="cv-pdf-gallery">
        {children}
    </ul>
);

