import { ShapePath, Vector2 } from 'three';

import { parseFloats } from './parseFloats';
import { getReflection } from './getReflection';
import { parseArcCommand } from './parseArcCommand';

export function parsePathD(d: string) {
    const path = new ShapePath();

    const point = new Vector2();
    const control = new Vector2();

    const firstPoint = new Vector2();
    let isFirstPoint = true;
    let doSetFirstPoint = false;


    const commands = d.match(/[a-df-z][^a-df-z]*/gi) || [];

    for (let i = 0, l = commands.length; i < l; i++) {
        const command = commands[i];

        const type = command.charAt(0);
        const data = command.substr(1).trim();


        if (isFirstPoint === true) {
            doSetFirstPoint = true;
            isFirstPoint = false;
        }

        switch (type) {
            case 'M': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    point.x = numbers[j + 0];
                    point.y = numbers[j + 1];
                    control.x = point.x;
                    control.y = point.y;
                    if (j === 0) {
                        path.moveTo(point.x, point.y);
                    } else {
                        path.lineTo(point.x, point.y);
                    }
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }
            case 'H': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j++) {
                    point.x = numbers[j];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'V': {
                const numbers = parseFloats(data)
                for (let j = 0, jl = numbers.length; j < jl; j++) {
                    point.y = numbers[j];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'L': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    point.x = numbers[j + 0];
                    point.y = numbers[j + 1];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'C': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 6) {
                    path.bezierCurveTo(
                        numbers[j + 0],
                        numbers[j + 1],
                        numbers[j + 2],
                        numbers[j + 3],
                        numbers[j + 4],
                        numbers[j + 5]
                    );
                    control.x = numbers[j + 2];
                    control.y = numbers[j + 3];
                    point.x = numbers[j + 4];
                    point.y = numbers[j + 5];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'S': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 4) {
                    path.bezierCurveTo(
                        getReflection(point.x, control.x),
                        getReflection(point.y, control.y),
                        numbers[j + 0],
                        numbers[j + 1],
                        numbers[j + 2],
                        numbers[j + 3]
                    );
                    control.x = numbers[j + 0];
                    control.y = numbers[j + 1];
                    point.x = numbers[j + 2];
                    point.y = numbers[j + 3];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'Q': {
                let numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 4) {
                    path.quadraticCurveTo(
                        numbers[j + 0],
                        numbers[j + 1],
                        numbers[j + 2],
                        numbers[j + 3]
                    );
                    control.x = numbers[j + 0];
                    control.y = numbers[j + 1];
                    point.x = numbers[j + 2];
                    point.y = numbers[j + 3];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'T': {
                const numbers = parseFloats(data)
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    const rx = getReflection(point.x, control.x);
                    const ry = getReflection(point.y, control.y);
                    path.quadraticCurveTo(rx, ry, numbers[j + 0], numbers[j + 1]);
                    control.x = rx;
                    control.y = ry;
                    point.x = numbers[j + 0];
                    point.y = numbers[j + 1];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'A': {
                const numbers = parseFloats(data)
                for (let j = 0, jl = numbers.length; j < jl; j += 7) {
                    const start = point.clone();
                    point.x = numbers[j + 5];
                    point.y = numbers[j + 6];
                    control.x = point.x;
                    control.y = point.y;
                    parseArcCommand(
                        path,
                        numbers[j],
                        numbers[j + 1],
                        numbers[j + 2],
                        numbers[j + 3],
                        numbers[j + 4],
                        start,
                        point
                    );

                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            //

            case 'm': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    point.x += numbers[j + 0];
                    point.y += numbers[j + 1];
                    control.x = point.x;
                    control.y = point.y;
                    if (j === 0) {
                        path.moveTo(point.x, point.y);
                    } else {
                        path.lineTo(point.x, point.y);
                    }
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'h': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j++) {
                    point.x += numbers[j];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'v': {
                const numbers = parseFloats(data)
                for (let j = 0, jl = numbers.length; j < jl; j++) {
                    point.y += numbers[j];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'l': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    point.x += numbers[j + 0];
                    point.y += numbers[j + 1];
                    control.x = point.x;
                    control.y = point.y;
                    path.lineTo(point.x, point.y);
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'c': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 6) {
                    path.bezierCurveTo(
                        point.x + numbers[j + 0],
                        point.y + numbers[j + 1],
                        point.x + numbers[j + 2],
                        point.y + numbers[j + 3],
                        point.x + numbers[j + 4],
                        point.y + numbers[j + 5]
                    );
                    control.x = point.x + numbers[j + 2];
                    control.y = point.y + numbers[j + 3];
                    point.x += numbers[j + 4];
                    point.y += numbers[j + 5];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 's': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 4) {
                    path.bezierCurveTo(
                        getReflection(point.x, control.x),
                        getReflection(point.y, control.y),
                        point.x + numbers[j + 0],
                        point.y + numbers[j + 1],
                        point.x + numbers[j + 2],
                        point.y + numbers[j + 3]
                    );
                    control.x = point.x + numbers[j + 0];
                    control.y = point.y + numbers[j + 1];
                    point.x += numbers[j + 2];
                    point.y += numbers[j + 3];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'q': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 4) {
                    path.quadraticCurveTo(
                        point.x + numbers[j + 0],
                        point.y + numbers[j + 1],
                        point.x + numbers[j + 2],
                        point.y + numbers[j + 3]
                    );
                    control.x = point.x + numbers[j + 0];
                    control.y = point.y + numbers[j + 1];
                    point.x += numbers[j + 2];
                    point.y += numbers[j + 3];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 't': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 2) {
                    const rx = getReflection(point.x, control.x);
                    const ry = getReflection(point.y, control.y);
                    path.quadraticCurveTo(
                        rx,
                        ry,
                        point.x + numbers[j + 0],
                        point.y + numbers[j + 1]
                    );
                    control.x = rx;
                    control.y = ry;
                    point.x = point.x + numbers[j + 0];
                    point.y = point.y + numbers[j + 1];
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            case 'a': {
                const numbers = parseFloats(data);
                for (let j = 0, jl = numbers.length; j < jl; j += 7) {
                    const start = point.clone();
                    point.x += numbers[j + 5];
                    point.y += numbers[j + 6];
                    control.x = point.x;
                    control.y = point.y;
                    parseArcCommand(
                        path,
                        numbers[j],
                        numbers[j + 1],
                        numbers[j + 2],
                        numbers[j + 3],
                        numbers[j + 4],
                        start,
                        point
                    );
                    if (j === 0 && doSetFirstPoint === true) {
                        firstPoint.copy(point);
                    }
                }
                break;
            }

            //

            case 'Z':
            case 'z': {
                path.currentPath.autoClose = true;
                if (path.currentPath.curves.length > 0) {
                    // Reset point to beginning of Path
                    point.copy(firstPoint);
                    path.currentPath.currentPoint.copy(point);
                    isFirstPoint = true;
                }
                break;
            }
        }

        // console.log( type, parseFloats( data ), parseFloats( data ).length  )

        doSetFirstPoint = false;
    }

    return path;
}
