import { useMemo } from 'react';
import { flatMap, orderBy, compact, min, max }  from 'lodash';

const ORDER_FORMAT = 'YYYYMMDD';

export function useTimelineRange(experience) {
    return useMemo(() => {
        const dates = orderBy(
            compact(
                flatMap(
                    experience,
                    ({start, end}) => [start, end]
                )
            ),
            (date => date.format(ORDER_FORMAT))
        );
        const rangeStart = dates[0];
        const rangeEnd = dates.slice().pop();
        const lengthList = experience
                    .map(({start, end}) => end.diff(start, 'days'));

        const minLength = min(lengthList);
        const maxLength = max(lengthList);

        const rangeLength = rangeEnd.diff(rangeStart, 'days');

        return {
            rangeStart,
            rangeEnd,
            rangeLength,
            minLength,
            lengthDistribution: maxLength - minLength
        };
    }, [experience]);
}