import React, {useMemo, HTMLProps } from 'react';
import classNames from 'classnames';
import { TransitionStatus } from 'react-transition-group/Transition';

import {AppImplementationContext} from './AppImplementationContext';

export function AppImplementation({
    implementation,
    status,
    timeout,
    ...props
}: HTMLProps<HTMLDivElement> & {
    implementation: string,
    status: TransitionStatus,
    timeout: number
}) {
    const {className} = props;

    const componentClassName = useMemo(() => classNames(
        'cv-app-implementation',
        `cv-${implementation}-app`,
        className,
        {
            [`cv-app-implementation-${status}`]: status
        }
    ), [className, status, implementation]);

    const context = useMemo(() => {
        const now = Date.now();
        return {
            status,
            start: now,
            end: now + timeout,
            timeout,
            implementation
        };
    }, [status, implementation, timeout]);


    return (
        <AppImplementationContext.Provider value={context}>
            <div
                {...props}
                data-implementation={implementation}
                className={componentClassName}
            />
        </AppImplementationContext.Provider>
    )
}