import {isEmpty,  partition, ValueIteratee} from 'lodash';


export function partitions<T extends object>(collection: T | null | undefined, callback?: ValueIteratee<T[keyof T]>, ...restPartitions: ValueIteratee<T[keyof T]>[])
: [any[], ...any[]] {
    const [positivePartition, negativePartition] = partition(collection, callback as ValueIteratee<T[keyof T]>);

    if (isEmpty(restPartitions) || isEmpty(negativePartition)) {
        return [positivePartition, negativePartition, ...restPartitions.map(() => [])];
    }

    return [
        positivePartition,
        ...partitions(negativePartition as T, ...restPartitions)
    ];
}