import React, { useMemo } from 'react';
import classNames from 'classnames';

type TrProps = JSX.IntrinsicElements['tr'];

export const Row = ({className, ...props}: TrProps) => (
    <tr
        className={useMemo(() => classNames(
            'cv-pdf-doc-row',
            className
        ), [className])}
        {...props}
    />
);