import React, { ReactChildren, useMemo, useState, useCallback, ReactNode } from "react";
import classNames from 'classnames';
import { animated as a, useSpring } from '@react-spring/web';
import { useHover } from 'react-use-gesture';
import { isString, size } from 'lodash';


import {Link}  from '../../Link';
import { useMediaQuery } from '../../../hooks';

const ALink = a(Link);

const configMassive = {mass: 1, tension: 800, friction: 7};
const configLight = {mass: .7, tension: 1000, friction: 25};
// const shadowColor = '#2b2a2a5e';
const shadowColor = '#2b2a2a54';

const LETTER_WEIGHT = .45;

export function Tag({
    className,
    to,
    children,
    mass,
    elevation = 1
}: {
    className?: string,
    to?: string | Location,
    children: ReactNode,
    mass?: number,
    elevation?: number
}) {
    const componentClassName = useMemo(() => classNames(
        'cv-pdf-tag',
        className
    ), [className]);

    const isPrint = useMediaQuery('print');

    const shadowScale = elevation * Number(!isPrint);

    const [hovered, setHovered] = useState(false);
    const [downed, setDown] = useState(false);
    const onPointerDown = useCallback(() => setDown(true), [setDown]);
    const onPointerUp = useCallback(() => setDown(false), [setDown]);
    const level = useMemo(() => (.15 - Number(downed) * .15) * Number(hovered) + .05 + Math.random() * .05, [downed, hovered]);
    const {rotation, x, y, shadowSize, shadowBlur} = useMemo(() => ({
        rotation: [
            1.7 * 7 * (Math.random() - .5) * level * elevation,
            1.7 * 7 * (Math.random() - .5) * level * elevation,
            1.7 * 2 * (Math.random() - .5)
        ],
        x: .1 * 2 * (Math.random() - .5),
        y: .1 * 2 * (Math.random() - .5),
        shadowSize: level * 1.7 * shadowScale,
        shadowBlur: level * .7 * shadowScale
    }), [level, isPrint, elevation]);

    const massConfig = useMemo(
        () => {
            const {
                mass: itemMass = configMassive.mass
                    * (
                        (1-LETTER_WEIGHT)
                        + LETTER_WEIGHT *
                        (isString(children)
                            ? size(children)
                            : 4
                        ) / 4
                    )

            } = {mass};

            return {
                ...configMassive,
                mass: itemMass
            }
        },
        [mass, children]
    );

    const {position, shadow} = useSpring({
        position: [x, y, level, ...rotation],
        shadow: [shadowSize, shadowBlur],
        config:  downed ? configLight : massConfig,
        immediate: isPrint
    });

    const style = {
        transform: position.to((x, y, level, rX, rY, rZ) => `translate(${x}vmin, ${y}vmin) rotateX(${rX}deg) rotateY(${rY}deg) rotateZ(${rZ}deg) translateY(-${level}vmin)`),
        boxShadow: shadow.to((shadowSize, shadowBlur) => `${shadowSize}vmin ${shadowSize}vmin ${shadowSize}vmin ${shadowBlur}vmin  ${shadowColor}`),
    };

    const bindHover = useHover(({ hovering }) => {
        setHovered(hovering);
        if (!hovering) {
            onPointerUp();
        }
    }, {
        pointerEvents: true
    });

    const props = {
        className: componentClassName,
        style,
        children,
        onPointerDown,
        onPointerUp,
        ...bindHover()
    };

    return to
        ? (
            <ALink
                inline
                {...props}
                to={to}
            />
        )
        : (
            <a.span
                {...props}
            />
        )
}
