import React, {useMemo, useCallback} from 'react';
import { useHistory } from 'react-router-dom';

import {usePositionLocation} from '../../../../../hooks';

export const DIRECTION_HORIZONTAL = 0;
export const DIRECTION_VERTICAL = 1;


export function TimeLinePositionLink({position, active, firstDate, dayRoom, margin, height}) {
    const positionLocation = usePositionLocation(position);
    const history = useHistory();

    const onPointerDown = useCallback(() => history.push(positionLocation), [history, positionLocation]);

    const color = useMemo(() => active ? '#9494f9' : '#4c4c6d', [active]);

    const {
        // offset,
        // length,
        client,
        x
    } = useMemo(() => {
        const offset = position.start.diff(firstDate, 'days') * dayRoom;
        const length = position.end.diff(position.start, 'days') * dayRoom;
        const client = length - margin;
        const x = offset + length / 2;
        return {
            offset,
            length,
            client,
            x
        };
    }, [position, firstDate, dayRoom, margin]);

    return (
        <mesh position={[x, 0, 0]} onPointerDown={onPointerDown}>
            <planeGeometry attach="geometry" args={[client, height]}/>
            <meshStandardMaterial
                attach="material"
                color={color}
                roughness={.5}
                metalness={.7}
        />
        </mesh>
    );
}