import React, {ReactChildren, useState, useMemo, useCallback, useEffect} from 'react';
import {useThree} from 'react-three-fiber';
import {PerspectiveCamera, Group} from 'three';

import {useNearFrustum} from '../hooks';

import {ToolsContext} from './ToolsContext';

export function ToolsFrustum({children} : {
    children?: ReactChildren
}) {
    const [maybeGroup, groupRef] = useState();
    const {camera} = useThree();
    const {clipBoxH, clipBoxW, dimensions, viewPortInitiated} = useNearFrustum(camera as PerspectiveCamera);


    const syncToolsFrustum = useCallback(() => {
        if (maybeGroup && camera) {
            const group = maybeGroup as any as Group;
            group.position.copy( camera.position );
            group.quaternion.copy( camera.quaternion );
            group.updateMatrix();
            group.translateZ( - camera.near );
        }
    }, [maybeGroup, camera]);

    useEffect(syncToolsFrustum, [maybeGroup]);

    const toolsContext = useMemo(() => {
        const landscape = clipBoxW >= clipBoxH;
        const portrait = !landscape;

        return {
            group: maybeGroup,
            clipBoxH,
            clipBoxW,
            landscape,
            portrait,
            dimensions,
            syncToolsFrustum,
            viewPortInitiated
        };
    }, [syncToolsFrustum, clipBoxH, clipBoxW, dimensions, maybeGroup, viewPortInitiated]);


    return (
        <>
            <group ref={groupRef}/>
            <ToolsContext.Provider value={toolsContext}>
                {children}
            </ToolsContext.Provider>
        </>
    );
}