import React, { useMemo } from 'react';
import classNames from 'classnames';

type TdProps = JSX.IntrinsicElements['td'];

export const Cell = ({className, ...props}: TdProps) => (
    <td
        className={useMemo(() => classNames(
            'cv-pdf-doc-cell',
            className
        ), [className])}
        {...props}
    />
);