import { ShapePath, Vector2 } from 'three';
import { svgAngle } from './svgAngel';

export     /**
* https://www.w3.org/TR/SVG/implnote.html#ArcImplementationNotes
* https://mortoray.com/2017/02/16/rendering-an-svg-elliptical-arc-as-bezier-curves/ Appendix: Endpoint to center arc conversion
* From
* rx ry x-axis-rotation large-arc-flag sweep-flag x y
* To
* aX, aY, xRadius, yRadius, aStartAngle, aEndAngle, aClockwise, aRotation
*/

function parseArcCommand(
    path: ShapePath,
    rx: number,
    ry: number,
    xAxisRotation: number,
    largeArc: number,
    sweep: number,
    start: Vector2,
    end: Vector2
) {
 const xAxisRotationRadians = (xAxisRotation * Math.PI) / 180

 // Ensure radii are positive
 rx = Math.abs(rx);
 ry = Math.abs(ry);

 // Compute (x1â², y1â²)
 const dx2 = (start.x - end.x) / 2.0;
 const dy2 = (start.y - end.y) / 2.0;
 const x1p = Math.cos(xAxisRotationRadians) * dx2 + Math.sin(xAxisRotationRadians) * dy2;
 const y1p = -Math.sin(xAxisRotationRadians) * dx2 + Math.cos(xAxisRotationRadians) * dy2;

 // Compute (cxâ², cyâ²)
 let rxs = rx * rx;
 let rys = ry * ry;
 const x1ps = x1p * x1p;
 const y1ps = y1p * y1p;

 // Ensure radii are large enough
 const cr = x1ps / rxs + y1ps / rys;

 if (cr > 1) {
   // scale up rx,ry equally so cr == 1
   const s = Math.sqrt(cr);
   rx = s * rx;
   ry = s * ry;
   rxs = rx * rx;
   rys = ry * ry;
 }

 const dq = rxs * y1ps + rys * x1ps;
 const pq = (rxs * rys - dq) / dq;
 const q = Math.sqrt(Math.max(0, pq)) * (1 - 2 * Number(largeArc === sweep));

 const cxp = q * rx * y1p / ry;
 const cyp = - q * ry * x1p / rx;

 // Step 3: Compute (cx, cy) from (cxâ², cyâ²)
 const cx = Math.cos(xAxisRotationRadians) * cxp - Math.sin(xAxisRotationRadians) * cyp + (start.x + end.x) / 2;
 const cy = Math.sin(xAxisRotationRadians) * cxp + Math.cos(xAxisRotationRadians) * cyp + (start.y + end.y) / 2;

 // Step 4: Compute Î¸1 and ÎÎ¸
 const theta = svgAngle(1, 0, (x1p - cxp) / rx, (y1p - cyp) / ry);
 const delta = svgAngle((x1p - cxp) / rx, (y1p - cyp) / ry, (-x1p - cxp) / rx, (-y1p - cyp) / ry) % (Math.PI * 2);

 path.currentPath.absellipse(cx, cy, rx, ry, theta, theta + delta, sweep === 0, xAxisRotationRadians);
}
