import React from 'react';

import {useExperience, PositionDescriptor} from '../../../hooks';

import {ExperiencePosition} from './ExperiencePosition';
import { Section, HeaderTitle, Footer, FooterTitle } from '../Document';

export function Experience() {
    const {experience, rangeLength} = useExperience();

    return (
        <Section className="cv-pdf-experience">
            <HeaderTitle>Experience.Artem Kudla</HeaderTitle>
            {
                experience.map((position, i) => (
                    <ExperiencePosition
                        key={position.id}
                        position={position}
                        prevPosition={experience[i+1] as PositionDescriptor}
                        nextPosition={experience[i-1] as PositionDescriptor}
                        rangeLength={rangeLength}
                    />
                ))
            }
            <Footer>
                <FooterTitle></FooterTitle>
            </Footer>
        </Section>
    )
}
