import React, { useMemo, ReactNode } from 'react'
import { flatten, isString, last, isFunction } from 'lodash';

import { TextOptions, useText } from './useText';
import { partitions } from '../../../utils';





export const Text = ({
    children,
    fontName,
    vAlign = 'baseline',
    hAlign = 'left',
    size = 72,
    ccw,
    width,
    color = 0xaaffaa,//'#000000',
    bevelEnabled = true,
    bevelThickness = .7,
    bevelSize = .5,
    bevelOffset = 0,
    bevelSegments = 8,
    x,
    y,
    material: Material = 'meshStandardMaterial',
    userData,
    ...props
}: {
    children: ReactNode,
    color?: string | number,
    position?: [number, number, number],
    bevelEnabled?: boolean,
    bevelThickness?: number,
    bevelSize?: number,
    bevelOffset?: number,
    bevelSegments?: number,
    userData?: {},
    material?: 'meshStandardMaterial'
} & TextOptions) => {
    const [texts, materials, subContent] = useMemo(() => {
            const expandedChildren = flatten([children]);

            return partitions(
                expandedChildren,
                isString,
                ({type}: {type?:  string | Function}) => /Material$/.test(type as string),
            )
        },
        [children]
    );

    const material = useMemo(() => last(materials) || (
            <Material
                attach="material"
                color={color}
                roughness={1}
                metalness={.1}
                // emissiveIntensity={.7}
            />
        ),
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [materials, Material, color]
    );

    const hasTales = Boolean(subContent.find(isFunction));

    const {shapes, metrics} = useText(texts as string[], {
        fontName,
        vAlign,
        hAlign,
        size,
        ccw,
        width,
        withMetrics: hasTales,
        x,
        y
    });



    // const extrudeSettings = {
    //     steps: 1,
    //     depth: .1,
    //     bevelEnabled: true,
    //     bevelThickness: .01,
    //     bevelSize: .05,
    //     bevelOffset: 0,
    //     bevelSegments: 3
    // };

    // useEffect(() => {
    //     if (texts.join('')  === 'Artem') {
    //         const g = new ExtrudeGeometry(shapes, extrudeSettings);
    //         const exp = new TypedGeometryExporter();
    //         const src = exp.parse(g);
    //         console.log(src);
    //         debugger;
    //     }
    // }, [texts, shapes])


    // <textGeometry key={children} attach="geometry" args={[children, config]} />
    // <extrudeGeometry attach="geometry"  args={[shapes, extrudeSettings]} />

    const renderedChildren = useMemo(
        () => React.Children.toArray(subContent.map((child: ReactNode) => (
            isFunction(child)
                ? child({metrics})
                : child
        ))),
        [subContent, metrics]
    );

    return (
        <group {...props}>
            <mesh scale={[1, -1, 1]} userData={userData}>
                <shapeBufferGeometry attach="geometry" args={[shapes, 5]} />

                {material}
            </mesh>
            {
                renderedChildren
            }
        </group>
    )
}