import React from 'react';
import { useClassNames } from '../../../../../../hooks';

type H2Props = JSX.IntrinsicElements['h2'];

export const FooterTitle = ({className, children, ...props}: H2Props) => (
    <h2
        className={useClassNames(
            [className],
            'cv-pdf-section-footer-title',
            className
        )}
        {...props}
    >
        {children}
    </h2>
);
