import { useMemo } from 'react'
import { compact, isUndefined,  flatten, toArray } from 'lodash';
import { Shape } from 'three';

import { TTSOptions, TTSWordMetrics } from '../../..';
import { useFont } from '../../../hooks/useFont';
import { parsePathD } from '../../../utils';

export type VerticalAlignType = 'baseline' | 'top' | 'middle' | 'bottom';
export type HorizontalAlignType = 'center' | 'left' | 'right';

export type TextOptions = {
    fontName: string,
    vAlign?: VerticalAlignType,
    hAlign?: HorizontalAlignType,
    size?: number | string,
    ccw?: boolean,
    width?: number,
    withMetrics?: boolean,
    x?: number,
    y?: number
};

export const useText = (textSource: string | string[], {
    fontName,
    vAlign = 'baseline',
    hAlign = 'left',
    size = 72,
    ccw = false,
    width,
    withMetrics,
    x,
    y
}: TextOptions): {
    shapes: Shape[],
    metrics?: TTSWordMetrics
} => {

    const svgFont = useFont(fontName);

    const text = toArray(textSource).map(String).join('');

    return useMemo(() => {
        const words = isUndefined(width)
            ? [text]
            : compact(text.split(/\s+/));
        const anchor = `${hAlign} ${vAlign}`;
        const fontScale = (size as number) / 3;

        const lastWord = words.length - 1;
        const fontHeight = svgFont.getHeight(fontScale);
        const {width: spaceWidth} = svgFont.getMetrics(' ', {fontSize: fontScale});

        const { shapes, metrics } = words
            .reduce(({shapes, options}: {shapes: Shape[][], options: TTSOptions, metrics?: TTSWordMetrics}, word: string, index) => {
                const d = svgFont.getD(word, options);
                const path = parsePathD(d);
                shapes.push(path.toShapes(ccw));

                if (index < lastWord || withMetrics) {
                    const metrics = svgFont.getMetrics(word, options)
                    const {width: wordWidth} = metrics;
                    const {x = 0, y = 0} = options;
                    if (Math.abs(x + wordWidth + spaceWidth) > (width as number)) {
                        return {
                            options: {
                                ...options,
                                x: 0,
                                y: y + Math.abs(fontHeight)
                            },
                            shapes,
                            metrics
                        };
                    }

                    return {
                        options: {
                            ...options,
                            x: x + wordWidth + spaceWidth,
                            y: y
                        },
                        shapes,
                        metrics
                    };
                }

                return {
                    shapes,
                    options,
                    metrics: undefined
                };
        }, {
            options: { x, y, fontSize: fontScale, anchor} as TTSOptions,
            shapes: [],
            metrics: undefined
        });

        return {
            metrics,
            shapes: flatten(shapes) as Shape[],
        };
    }, [text, size, hAlign, vAlign, ccw, svgFont, x, y, width, withMetrics]);
};