import React, { Suspense, useState, useEffect } from 'react';
import { animated as a, useTransition,  SpringValue  } from '@react-spring/three';
import { get, zipObject } from 'lodash';
import { ShapePath, Color, DoubleSide, Shape } from 'three';
// import { } from '@react-spring/web';

import { PositionDescriptor } from '../../../hooks';
import { Text } from '../Text';
import { SVGLoader } from '../../../utils';
import { TTSWordMetrics } from '../../..';

const positionNames = [
    'position',
    '2015-03-01-leantegra-inc',
    '2016-05-01-leantegra-inc',
    '2017-11-02-agile-stacks-inc'
];
const svgs = zipObject(
    positionNames,
    positionNames
        .map(name => ({
            name,
            url: `/images/${name}.svg`
        }))
        .map(
            ({name, url}) =>
            new Promise(
                resolve => new SVGLoader().load(
                    url,
                    (
                        shapes: (ShapePath & {color: Color})[]
                    ) => resolve(
                        shapes.map(
                            (group, index) => ({
                                shapes: group.toShapes(true),
                                color: group.color,
                                index,
                                userData: {name}
                            })
                        )
                    )
                )
            )
        )
);

type Shapes = {shapes: (Shape &  {uuid: string})[]}[];

type Item = {
    shapes: Shape[],
    userData?: {},
    index?: number
};

type SpringProps = {
    rotation: SpringValue<number[]>,
    position: SpringValue<number[]>,
    color?: string | number,
    opacity: SpringValue<number>
};

const RShape = ({ shapes, rotation, position, color, opacity, index = 0, userData = {} } : {
} & Item & SpringProps) => (
    <a.mesh
        rotation={rotation}
        position={position.to((x: number, y: number, z: number) => [x, y, z + index * 3])}
        scale={[.07, -.07, .07]}
        userData={{
            ...userData,
            shapes,
            // shape,
            // index: shapes.indexOf(shape)
        }}
    >
        <meshPhongMaterial attach="material" color={color}  side={DoubleSide} depthWrite={false} transparent />
        <shapeBufferGeometry attach="geometry" args={[shapes]} />
        {/* <meshStandardMaterial
            attach="material"
            // color={0xaaffaa}
            color={color}
            roughness={.1}
            metalness={.7}
        /> */}
    </a.mesh>
);

export const ExperiencePosition = ({
    experiencePosition,
    state,
    fontName,
    ccw,
    ...props
}: {
    experiencePosition: PositionDescriptor,
    position?: [number, number, number],
    fontName: string,
    ccw: boolean,
    state?: string
}) => {
    // const active = useMemo(() => Boolean(state) && ['entered', 'entering'].includes(state as string), [state]);
    // const {
    //     rotation,
    //     position
    // } = useSpring({
    //     rotation: active ? [0, 0, 0] : [0, 0, 0],//[Math.PI, Math.PI, 0],
    //     position: active ? [0, 0, -5] : [0, 0, -5],//[0, 10, -50],
    //     // position: active ? [0, 0, 0] : [0, 0, 0],//[0, 10, -50],
    //     config: {mass: 20, tension: 7000, friction: 350}
    // });
    const [shapes, setShapes] = useState([] as Shapes);

    const {[get(experiencePosition, 'id')]: scene = svgs.position} = svgs;
    useEffect(
        () => {
            scene.then(setShapes as (value: unknown)=> any);
        },
        [scene, experiencePosition]
    );

    const transitions = useTransition(shapes, {
        keys: (item: any) => item.shapes[0].uuid,
        from: { rotation: [-0.2, 0.9, 0], position: [0, 50, -200], opacity: 0 },
        enter: { rotation: [0, 0, 0], position: [0, 0, 0], opacity: 1 },
        // leave: { rotation: [0, 0, 0], position: [0, 0, 0], opacity: .1 },
        leave: { rotation: [0.2, -0.9, 0], position: [0, -400, 200], opacity: 0 },
        config: { mass: 30, tension: 800, friction: 190, precision: 0.0001 },
        // config: { mass: 30, tension: 800, friction: 190, precision: 0.01 },
        ...{ order: ['leave', 'enter', 'update'], trail: 15, lazy: true, unique: true, reset: false }
      });

    const fontSize = 24;



    return (
        <>
            <group {...props} >
                <Suspense fallback={
                    <mesh>
                        <sphereGeometry attach="geometry" args={[3, 30, 10]} />
                        <meshBasicMaterial attach="material" color={0xff0000} wireframe />
                    </mesh>
                }>

                    {
                        transitions((props, item, {key}) => (
                            <RShape
                                // key={key}
                                {...(item as Item)}
                                {...(props as SpringProps)}
                            />
                        ))
                    }

                    <Text
                        size={fontSize}
                        fontName="/fonts/micro-extend/MicroExtendFLF-BoldItalic.ttf"
                        vAlign="bottom"
                        position={[0, 50, 0]}
                        width={70}
                        color="#f22"
                        userData={{
                            experiencePosition,
                            text: experiencePosition.name
                        }}

                    >
                        {experiencePosition.name}
                        {
                            ({metrics: {height, y}}: {metrics: TTSWordMetrics}) => (
                                <group position={[0, 0, -7]}>
                                    <Text
                                        y={y + height}
                                        size={fontSize * 2 / 3}
                                        vAlign="top"
                                        fontName="/fonts/micro-extend/MicroExtendFLF.ttf"
                                        // fontName="/fonts/quarca/QuarcaExtLight.otf"
                                        userData={{
                                            experiencePosition,
                                            text: experiencePosition.company
                                        }}
                                    >
                                        {' '}@{experiencePosition.company} ({experiencePosition.city})
                                        {
                                            ({metrics: {height, y}}: {metrics: TTSWordMetrics}) => (
                                                <group position={[0, 0, 3]}>
                                                    <Text
                                                        y={y + height}
                                                        size={fontSize * (2 / 3) ** 3}
                                                        vAlign="top"
                                                        fontName="/fonts/micro-extend/MicroExtendFLF.ttf"
                                                        // fontName="/fonts/quarca/QuarcaExtLight.otf"
                                                        // position={[0, 0, 14]}
                                                        userData={{
                                                            experiencePosition,
                                                            text: experiencePosition.companyProduct
                                                        }}
                                                    >
                                                        {experiencePosition.companyProduct}
                                                    </Text>
                                                </group>
                                            )

                                        }

                                    </Text>
                                </group>
                            )
                        }
                    </Text>

                </Suspense>
            </group>
        </>
    );
};
