import React, { useMemo, useCallback } from 'react';
import { NavLink } from 'react-router-dom';

import { usePositionLocation } from '../../../../../hooks';
import { useImplementation } from '../../../../AppImplementation';

const usePositionTimeLine = (position, prevPosition) => useMemo(
    () => {
        const {end} = position;
        const start = prevPosition
            ? prevPosition.end
            : position.start;

        const length = end.diff(start, 'days');
        const offset = start !== position.start
            && 100 * position.start.diff(start, 'days') / length;

        return {
            length,
            offset
        };
    },
    [position, prevPosition]
);

export function TimeLinePositionLink({position, prevPosition, order}) {
    const positionLocation = usePositionLocation(position);

    const {
        length,
        offset
    } = usePositionTimeLine(position, prevPosition);

    const {
        status,
        timeout
    } = useImplementation();


    const transitTime = timeout / 1.7;
    const transform = useMemo(() => ['exited','exiting'].includes(status) && `translate(${5 * (1 - 2 * (order + .2 * (Math.random()  - .5)))}vmin, ${(Math.random() + .5) * 60}vmin`, [status, order]);
    const transition = useMemo(() => status !== 'exited-' && `transform ${transitTime * (1 - 1 / 4 * order)}ms ${transitTime / 3 * order}ms linear`, [status, order, transitTime]);

    const linkStyle = useMemo(() => ({transition,  transform}), [transition,  transform])
    const style={
        flex: length
    };

    const ref = useCallback(link => link && link.clientHeight, []);
    return (
        <div
            className="cv-experience-position"
            style={style}
        >
            {
                Boolean(offset) && (
                    <span
                        style={{
                            flexBasis: `${offset}%`
                        }}
                    />
                )
            }
            <NavLink
                ref={ref}
                className="cv-experience-position-link"
                to={positionLocation}
                style={linkStyle}
            >
                {position.company}
            </NavLink>
        </div>
    );
}