import { flatten, kebabCase } from 'lodash';
import moment, { Moment } from 'moment';

const now = moment.utc();
const TIMELINE_DATE_FORMAT = 'YYYY-MM-DD';

export interface PositionDescriptor {
    timeline: Moment[],
    start: Moment,
    end: Moment,
    startText: string,
    endText: string,
    id: string,

    name: string,
    company: string,
    companyProduct: string,
    city: string,
    linkCompany?: string,
    description: string | string[],
    technologies: string[]
};

type PositionDescriptorAttributes = {
    timeline: string[] | string,
    company: string,
    name: string,
    companyProduct: string,
    city: string,
    linkCompany?: string,
    description: string | string[],
    technologies: string[]
};

export const PositionDescriptor = function(this:PositionDescriptor, attributes:  PositionDescriptorAttributes) {
    if (!(this instanceof PositionDescriptor)) {
        return new PositionDescriptor(attributes);
    }
    const [startSource, endSource] = flatten([attributes.timeline]) as string[];
    const timeline = [startSource, endSource].map(date => moment.utc(date || now, TIMELINE_DATE_FORMAT));
    const [start, end] = timeline;
    const [startText, endText] = [start, end].map(date => date.format(TIMELINE_DATE_FORMAT));
    const id = kebabCase(`${startText} ${attributes.company}`);
    Object.assign(this, attributes, {timeline, start, end, startText, endText, id});
} as any as { new (attributes: PositionDescriptorAttributes): PositionDescriptor; };