import React, { useMemo } from 'react';
import { flatMap, first, last } from 'lodash';
import moment from 'moment';

import { useExperience } from '../../../../hooks';
import { Tool, useTools } from '../../Tools';

import {TimeLinePositionLink, DIRECTION_HORIZONTAL, DIRECTION_VERTICAL} from './TimeLinePositionLink';

const DATE_FORMAT = 'YYYYMMDD';

export function ExperienceTimeline({experience}) {
    const {dimensions, landscape} = useTools();
    const {currentPosition} = useExperience();

    const {
        firstDate,
        experienceDuration
    } = useMemo(() => {
        const dates = flatMap(
            experience,
            ({start, end}) => [start.format(DATE_FORMAT), end.format(DATE_FORMAT)]
        ).sort();

        const [firstDate, lastDate] = [first(dates), last(dates)].map(date => moment.utc(date, DATE_FORMAT));
        return {
            firstDate,
            experienceDuration: lastDate.diff(firstDate, 'days'),
        }
    }, [experience]);

    const {
        dayRoom,
        // experienceRoom,
        // experienceHost,
        experienceMargin,
        direction,
        position,
        height
    } = useMemo(() => {
        const [clipBoxW, clipBoxH] = dimensions;
        const direction = landscape
            ? DIRECTION_HORIZONTAL
            : DIRECTION_VERTICAL;
        const experienceRoom = Math.max(...dimensions);
        const experienceHost = Math.min(...dimensions);
        const dayRoom = experienceRoom / (experienceDuration | 1);
        const experienceMargin = experienceRoom / 300;

        const height = experienceHost * .05;

        const position = direction === DIRECTION_HORIZONTAL
            ? [-clipBoxW / 2, experienceHost * (.5 - .03) - height / 2, 0]
            : [-experienceHost * (.5-.03) + height / 2, -clipBoxH / 2, 0];
        return {
            dayRoom,
            experienceRoom,
            experienceHost,
            experienceMargin,
            direction,
            position,
            height
        }
    }, [dimensions, landscape, experienceDuration]);

    return (
        <Tool>
            <group rotation={[0,0,  direction * Math.PI / 2]} position={position}>
                {
                    experience.map(position => (
                        <TimeLinePositionLink
                            key={position.id}
                            position={position}
                            active={position === currentPosition}
                            firstDate={firstDate}
                            dayRoom={dayRoom}
                            margin={experienceMargin}
                            height={height}
                        />
                    ))
                }
            </group>
        </Tool>
    );
}