import {useMemo} from 'react';
import {useRouteMatch} from 'react-router-dom';


import {experience} from './experience';
import {PositionDescriptor} from './PositionDescriptor';

import {useCurrentPosition} from './useCurrentPosition';
import {useTimelineRange} from './useTimelineRange';
import { Moment } from 'moment';

export function useExperience(): {
    experience: PositionDescriptor[],
    currentPosition?: PositionDescriptor,
    rangeStart: Moment,
    rangeEnd: Moment,
    rangeLength: number,
    minLength: number,
    lengthDistribution: number
} {
    const match = useRouteMatch();
    const currentPosition = useCurrentPosition(match, experience);
    const timelineRange = useTimelineRange(experience);

    return useMemo(() => ({
        experience,
        ...currentPosition,
        ...timelineRange
    }), [
        currentPosition,
        timelineRange
    ]);
}