import React, { useMemo, ReactChildren, forwardRef } from 'react';
import { LinkProps, Link as RouterLink } from 'react-router-dom';
import classNames from 'classnames';
import {isUndefined} from 'lodash';

const URL_SCHEMA = /^(.+?:)?\/\//;

function normalizeUrl(url?: {} | string | ReactChildren | null) {
    return String(url)
        .replace(URL_SCHEMA, '')
        .replace(/\/$/, '')
}

export const Link = forwardRef(({
    to,
    className,
    inline,
    ...props
}: {
    to: string | Location,
    text?: ReactChildren,
    inline?: boolean
} & LinkProps, ref) => {
    const href = String(to);
    const {children, text = children} = props;

    const isInline = useMemo(
        () => isUndefined(inline)  ? normalizeUrl(href) === normalizeUrl(text) : inline,
        [href, text, inline]
    );

    const isExternal = useMemo(() => URL_SCHEMA.test(href), [href]);

    const componentClassName = useMemo(() => classNames(
        'cv-pdf-link',
        className,
        {
            'cv-pdf-link-inline': isInline,
            'cv-pdf-link-external': isExternal,
            'cv-pdf-link-internal': !isExternal
        }
    ), [className, isInline, isExternal]);

    if (isExternal) {
        return (
            <a
                className={componentClassName}
                href={href}
                target="__blank"
                rel="noopener"
                ref={ref as any}
                {...props}
            >{text}</a>
        )
    }

    return (
        <RouterLink
            ref={ref as any}
            className={componentClassName}
            to={to}
            {...props}
        />
    );
});