import { useState, useEffect } from 'react';

const emptyObject: {matches?: boolean} = {};
export const useMediaQuery = (query: string, defaultValue?: boolean) => {
    const maybeMqList = 'matchMedia' in window
        ? window.matchMedia(query)
        : emptyObject;

    const [stateIgnored, setState] = useState(null as any as MediaQueryListEvent);

    useEffect(
        () => {
            if (maybeMqList !== emptyObject) {
                const mqList = maybeMqList as MediaQueryList;

                mqList.addListener(setState);

                return () => mqList.removeListener(setState);
            }
        },
        [query]
    );

    const {
        matches = defaultValue
    } = maybeMqList;

    return matches;
};
