import React from 'react';

import {TimeLinePositionLink} from './TimeLinePositionLink';

export function ExperienceTimeline({experience}) {
    return (
        <div className="cv-experience-timeline">
                {
                    experience.map((position, index) => (
                        <TimeLinePositionLink
                            key={position.id}
                            position={position}
                            prevPosition={experience[index+1]}
                            order={index / experience.length}
                        />
                    ))
                }
            </div>
    );
}