import React from 'react';
import {Link} from 'react-router-dom';
import { useAppImplementationLocation } from '../../hooks/useAppImplementationLocation';

export const CAPABILITY_WARNING = 'Your browser does not support webgl.';
export function CapabilityWarning({defaultApp}: {defaultApp?: string}) {
    const fallbackLocation = useAppImplementationLocation(defaultApp);

    return (
        <div className="cv-webgl-app-capability-warning">
            {CAPABILITY_WARNING}
            {
                fallbackLocation
                && (<>
                    Please fallback to <Link className="cv-webgl-app-fallback-link" to={fallbackLocation}>supported version</Link>.
                </>)
            }
        </div>
    );
}