import React, { useMemo } from 'react';
import { useTools } from "./Tools";
import { useState, useLayoutEffect } from "react";
import { useThree } from "react-three-fiber";


export function ViewControl() {
    const {camera, gl} = useThree();
    const [controls, controlsRef] = useState();
    const {syncToolsFrustum} = useTools();

    const {aspect} = camera;
    const args = useMemo(() => aspect && [camera, gl.domElement], [aspect, camera, gl.domElement]);

    useLayoutEffect(() => {
        if (controls && syncToolsFrustum) {
            controls.addEventListener('change', syncToolsFrustum);

            syncToolsFrustum();

            return () => controls.removeEventListener('change', syncToolsFrustum);
        }
    }, [controls, syncToolsFrustum]);

    return (
        args  && <orbitControls ref={controlsRef} args={args} />
    );
}