import React, { AnchorHTMLAttributes } from 'react';
import classNames from 'classnames';
import {Link, LinkProps} from 'react-router-dom';
import { ReactComponentLike } from 'prop-types';

type  Links = LinkProps | AnchorHTMLAttributes<HTMLAnchorElement>;
type ToolButtonProps = Links & {
    component?: ReactComponentLike, className?: string
};
export function ToolButton({component: Component  = Link, className, ...props}: ToolButtonProps) {
    const componentClassName = classNames(
        'cv-tool-button',
        className
    )
    return (
        <Component className={componentClassName} {...props}/>
    );
}