import React, { ReactChildren } from 'react';
import { useTechnologyLink } from '../../hooks';

import {Tag} from './Tag';

export const TechnologyTag = ({
    children = '',
    technology = children,
    ...props
}: {
    children?: ReactChildren | string,
    technology?: ReactChildren | string,
    className?: string
}) => {
    const technologyLink = useTechnologyLink(String(technology));

    return (
        <Tag
            to={technologyLink}
            {...props}
        >
            {technology}
        </Tag>
    );
}