import {createContext, useContext} from 'react';
import { Group } from 'three';

export type ToolsContextType = {
    group: Group,
    clipBoxH: number,
    clipBoxW: number,
    dimensions: number[],
    syncToolsFrustum: Function,
    landscape: boolean,
    portrait:  boolean,
    viewPortInitiated: boolean
}
export const ToolsContext = createContext({});

export const useTools = () => useContext(ToolsContext) as ToolsContextType;
