import { DefaultLoadingManager, FileLoader, Matrix3, Path, ShapePath, Vector2, Vector3 } from 'three';
import { parseFloats } from './parseFloats';
import { parsePathD } from './parsePathD';

var SVGLoader = function(manager) {
  this.manager = manager !== undefined ? manager : DefaultLoadingManager
}

SVGLoader.prototype = {
  constructor: SVGLoader,

  load: function(url, onLoad, onProgress, onError) {
    var scope = this

    var loader = new FileLoader(scope.manager)
    loader.setPath(scope.path)
    loader.load(
      url,
      function(text) {
        onLoad(scope.parse(text))
      },
      onProgress,
      onError
    )
  },

  setPath: function(value) {
    this.path = value
    return this
  },

  parse: function(text) {
    function parseNode(node, style) {
      if (node.nodeType !== 1) return

      var transform = getNodeTransform(node)

      var path = null

      switch (node.nodeName) {
        case 'defs': {
          inDefinition = true;
          break;
        }
        case 'svg':
          break

        case 'g':
          style = parseStyle(node, style)
          break


        case 'use':
          const def = defs[node.getAttribute('xlink:href')];
          if (!def) {
            break;
          }
          node = node.cloneNode();
          node.setAttribute('d', def.getAttribute('d'));


        // eslint disable-next-line no-fallthrough
        case 'path':
          if (inDefinition) {
            if (node.hasAttribute('id')) {
              defs[`#${node.getAttribute('id')}`] = node;
            }

          } else {
            style = parseStyle(node, style)
            if (node.hasAttribute('d') && isVisible(style)) path = parsePathNode(node, style)
          }
          break


        case 'rect':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parseRectNode(node, style)
          break

        case 'polygon':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parsePolygonNode(node, style)
          break

        case 'polyline':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parsePolylineNode(node, style)
          break

        case 'circle':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parseCircleNode(node, style)
          break

        case 'ellipse':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parseEllipseNode(node, style)
          break

        case 'line':
          style = parseStyle(node, style)
          if (isVisible(style)) path = parseLineNode(node, style)
          break
        default:
      }

      if (path) {
          transformPath(path, currentTransform)

          paths.push(path)
      }

      var nodes = node.childNodes

      for (var i = 0; i < nodes.length; i++) {
        parseNode(nodes[i], style)
      }

      if (transform) {
        currentTransform.copy(transformStack.pop())
      }

      inDefinition = false;
    }

    function parsePathNode(node, style) {
      const path = parsePathD(node.getAttribute('d'));
      path.color.setStyle(style.fill);
      return path;
    }



    /*
     * According to https://www.w3.org/TR/SVG/shapes.html#RectElementRXAttribute
     * rounded corner should be rendered to elliptical arc, but bezier curve does the job well enough
     */
    function parseRectNode(node, style) {
      var x = parseFloat(node.getAttribute('x') || 0)
      var y = parseFloat(node.getAttribute('y') || 0)
      var rx = parseFloat(node.getAttribute('rx') || 0)
      var ry = parseFloat(node.getAttribute('ry') || 0)
      var w = parseFloat(node.getAttribute('width'))
      var h = parseFloat(node.getAttribute('height'))

      var path = new ShapePath()
      path.color.setStyle(style.fill)
      path.moveTo(x + 2 * rx, y)
      path.lineTo(x + w - 2 * rx, y)
      if (rx !== 0 || ry !== 0) path.bezierCurveTo(x + w, y, x + w, y, x + w, y + 2 * ry)
      path.lineTo(x + w, y + h - 2 * ry)
      if (rx !== 0 || ry !== 0) path.bezierCurveTo(x + w, y + h, x + w, y + h, x + w - 2 * rx, y + h)
      path.lineTo(x + 2 * rx, y + h)

      if (rx !== 0 || ry !== 0) {
        path.bezierCurveTo(x, y + h, x, y + h, x, y + h - 2 * ry)
      }

      path.lineTo(x, y + 2 * ry)

      if (rx !== 0 || ry !== 0) {
        path.bezierCurveTo(x, y, x, y, x + 2 * rx, y)
      }

      return path
    }

    function parsePolygonNode(node, style) {
      function iterator(match, a, b) {
        var x = parseFloat(a)
        var y = parseFloat(b)

        if (index === 0) {
          path.moveTo(x, y)
        } else {
          path.lineTo(x, y)
        }

        index++
      }

      var regex = /(-?[\d.?]+)[,|\s](-?[\d.?]+)/g

      var path = new ShapePath()
      path.color.setStyle(style.fill)

      var index = 0

      node.getAttribute('points').replace(regex, iterator)

      path.currentPath.autoClose = true

      return path
    }

    function parsePolylineNode(node, style) {
      function iterator(match, a, b) {
        var x = parseFloat(a)
        var y = parseFloat(b)

        if (index === 0) {
          path.moveTo(x, y)
        } else {
          path.lineTo(x, y)
        }

        index++
      }

      var regex = /(-?[\d.?]+)[,|\s](-?[\d.?]+)/g

      var path = new ShapePath()
      path.color.setStyle(style.fill)


      var index = 0

      node.getAttribute('points').replace(regex, iterator)

      path.currentPath.autoClose = false

      return path
    }

    function parseCircleNode(node, style) {
      var x = parseFloat(node.getAttribute('cx'))
      var y = parseFloat(node.getAttribute('cy'))
      var r = parseFloat(node.getAttribute('r'))

      var subpath = new Path()
      subpath.absarc(x, y, r, 0, Math.PI * 2)

      var path = new ShapePath()
      path.color.setStyle(style.fill)
      path.subPaths.push(subpath)

      return path
    }

    function parseEllipseNode(node, style) {
      var x = parseFloat(node.getAttribute('cx'))
      var y = parseFloat(node.getAttribute('cy'))
      var rx = parseFloat(node.getAttribute('rx'))
      var ry = parseFloat(node.getAttribute('ry'))

      var subpath = new Path()
      subpath.absellipse(x, y, rx, ry, 0, Math.PI * 2)

      var path = new ShapePath()
      path.color.setStyle(style.fill)
      path.subPaths.push(subpath)

      return path
    }

    function parseLineNode(node, style) {
      var x1 = parseFloat(node.getAttribute('x1'))
      var y1 = parseFloat(node.getAttribute('y1'))
      var x2 = parseFloat(node.getAttribute('x2'))
      var y2 = parseFloat(node.getAttribute('y2'))

      var path = new ShapePath()
      path.moveTo(x1, y1)
      path.lineTo(x2, y2)
      path.currentPath.autoClose = false

      return path
    }

    //

    function parseStyle(node, style) {
      style = Object.assign({}, style) // clone style

      if (node.hasAttribute('fill')) style.fill = node.getAttribute('fill')
      if (node.style.fill !== '') style.fill = node.style.fill

      return style
    }

    function isVisible(style) {
      return style.fill !== 'none' && style.fill !== 'transparent'
    }

    function getNodeTransform(node) {
      if (!node.hasAttribute('transform')) {
        return null
      }

      var transform = parseTransformNode(node)

      if (transform) {
        if (transformStack.length > 0) {
          transform.premultiply(transformStack[transformStack.length - 1])
        }

        currentTransform.copy(transform)
        transformStack.push(transform)
      }

      return transform
    }

    function parseTransformNode(node) {
      var transform = new Matrix3()
      var currentTransform = tempTransform0
      var transformsTexts = node.getAttribute('transform').split(' ')

      for (var tIndex = transformsTexts.length - 1; tIndex >= 0; tIndex--) {
        var transformText = transformsTexts[tIndex]
        var openParPos = transformText.indexOf('(')
        var closeParPos = transformText.indexOf(')')

        if (openParPos > 0 && openParPos < closeParPos) {
          var transformType = transformText.substr(0, openParPos)

          var array = parseFloats(transformText.substr(openParPos + 1, closeParPos - openParPos - 1))

          currentTransform.identity()

          switch (transformType) {
            case 'translate':
              if (array.length >= 1) {
                var tx = array[0]
                var ty = tx

                if (array.length >= 2) {
                  ty = array[1]
                }

                currentTransform.translate(tx, ty)
              }

              break

            case 'rotate':
              if (array.length >= 1) {
                var angle = 0
                var cx = 0
                var cy = 0

                // Angle
                angle = (-array[0] * Math.PI) / 180

                if (array.length >= 3) {
                  // Center x, y
                  cx = array[1]
                  cy = array[2]
                }

                // Rotate around center (cx, cy)
                tempTransform1.identity().translate(-cx, -cy)
                tempTransform2.identity().rotate(angle)
                tempTransform3.multiplyMatrices(tempTransform2, tempTransform1)
                tempTransform1.identity().translate(cx, cy)
                currentTransform.multiplyMatrices(tempTransform1, tempTransform3)
              }

              break

            case 'scale':
              if (array.length >= 1) {
                var scaleX = array[0]
                var scaleY = scaleX

                if (array.length >= 2) {
                  scaleY = array[1]
                }

                currentTransform.scale(scaleX, scaleY)
              }

              break

            case 'skewX':
              if (array.length === 1) {
                currentTransform.set(1, Math.tan((array[0] * Math.PI) / 180), 0, 0, 1, 0, 0, 0, 1)
              }

              break

            case 'skewY':
              if (array.length === 1) {
                currentTransform.set(1, 0, 0, Math.tan((array[0] * Math.PI) / 180), 1, 0, 0, 0, 1)
              }

              break

            case 'matrix':
              if (array.length === 6) {
                currentTransform.set(array[0], array[2], array[4], array[1], array[3], array[5], 0, 0, 1)
              }

              break

            default:
          }
        }

        transform.premultiply(currentTransform)
      }

      return transform
    }

    function transformPath(path, m) {
      function transfVec2(v2) {
        tempV3.set(v2.x, v2.y, 1).applyMatrix3(m)

        v2.set(tempV3.x, tempV3.y)
      }

      // var isRotated = isTransformRotated(m)

      var tempV2 = new Vector2()
      var tempV3 = new Vector3()

      var subPaths = path.subPaths

      for (var i = 0, n = subPaths.length; i < n; i++) {
        var subPath = subPaths[i]
        var curves = subPath.curves

        for (var j = 0; j < curves.length; j++) {
          var curve = curves[j]

          if (curve.isLineCurve) {
            transfVec2(curve.v1)
            transfVec2(curve.v2)
          } else if (curve.isCubicBezierCurve) {
            transfVec2(curve.v0)
            transfVec2(curve.v1)
            transfVec2(curve.v2)
            transfVec2(curve.v3)
          } else if (curve.isQuadraticBezierCurve) {
            transfVec2(curve.v0)
            transfVec2(curve.v1)
            transfVec2(curve.v2)
          } else if (curve.isEllipseCurve) {
            tempV2.set(curve.aX, curve.aY)
            transfVec2(tempV2)
            curve.aX = tempV2.x
            curve.aY = tempV2.y

            curve.xRadius *= getTransformScaleX(m)
            curve.yRadius *= getTransformScaleY(m)
          }
        }
      }
    }

    // function isTransformRotated(m) {
    //   return m.elements[1] !== 0 || m.elements[3] !== 0
    // }

    function getTransformScaleX(m) {
      var te = m.elements
      return Math.sqrt(te[0] * te[0] + te[1] * te[1])
    }

    function getTransformScaleY(m) {
      var te = m.elements
      return Math.sqrt(te[3] * te[3] + te[4] * te[4])
    }

    //

    var paths = []

    var defs = {};
    var inDefinition = false;

    var transformStack = []

    var tempTransform0 = new Matrix3()
    var tempTransform1 = new Matrix3()
    var tempTransform2 = new Matrix3()
    var tempTransform3 = new Matrix3()

    var currentTransform = new Matrix3()

    var xml = new DOMParser().parseFromString(text, 'image/svg+xml') // application/xml

    parseNode(xml.documentElement, { fill: '#000' })

    return paths
  }
}

export { SVGLoader }
