import Axios, { AxiosRequestConfig } from 'axios';
import { useMemo } from 'react';
import { PromiseWrapper } from '../utils';

const { PUBLIC_URL } = process.env;

const axios = Axios.create({
    baseURL: PUBLIC_URL
});

type AxiosHookConfig = string | AxiosRequestConfig & {
    cache?: boolean | string,
    ttl?: number | null,
    expire?: number | null
};
const DEFAULT_TTL = 30 * 60 * 1000;
const requestsCache: {
    [Identifier: string]: {
        expire?: number | null,
        data: PromiseWrapper
    }
} = {};
export const useAxios = (config: AxiosHookConfig) => {
    const normalizedConfig = useMemo(
        () => typeof config === 'string'
            ? {url: config}
            : config,
        [config]
    );

    const {
        url = '/',
        method = 'get'
    } = normalizedConfig;
    const {
        cache = true,
    } = normalizedConfig;

    const cacheKey = typeof cache === 'string'
        ? cache
        : cache && `${method}|${url}`;

    if (cacheKey) {
        const cachedRecord = requestsCache[cacheKey];
        if (cachedRecord) {
            if (!cachedRecord.expire || cachedRecord.expire > Date.now()) {
                return cachedRecord.data.read();
            }
        }
    }
    const request = PromiseWrapper.from(axios(normalizedConfig));
    if (cacheKey) {
        const {
            ttl = DEFAULT_TTL
        } = normalizedConfig;
        const {
            expire = ttl ?  (Date.now() + ttl) : undefined
        } = normalizedConfig;

        requestsCache[cacheKey] = {
            data: request,
            ...expire && {expire}
        };
    }

    return request.read();
};