import {useMemo} from 'react';
import {PerspectiveCamera} from 'three';

export function useNearFrustum(camera: PerspectiveCamera) {
    const {fov, near, aspect} = camera || {} as PerspectiveCamera;

    return useMemo(() => {
        const clipBoxH = 2 * Math.tan(fov * Math.PI / 180 / 2) * near; // height
        const clipBoxW = clipBoxH * aspect; // width

        const viewPortInitiated = !isNaN(aspect);

        return {
            near,
            clipBoxH,
            clipBoxW,
            dimensions: [clipBoxW, clipBoxH],
            viewPortInitiated
        }
    }, [fov, near, aspect]);
}