import React, { useMemo, ReactNode, useCallback, ReactElement } from 'react';
import {uniq} from 'lodash';
import {
  Switch,
  Route,
  Redirect,
  RouteProps,
  useRouteMatch
} from 'react-router-dom';
import { TransitionGroup, Transition } from 'react-transition-group';

import {
  HtmlApp,
  WebglApp,
  PdfApp,
  Toolbar,
  CapabilityWarning,
  AppImplementation
} from './components';


import './CvApp.scss';
import { Location, LocationDescriptor } from 'history';
import { useAppImplementationLocation } from './hooks/useAppImplementationLocation';

const ApplicationProvider = ({
  children,
  ...props
}: { children: ReactNode } & RouteProps) => (
    <Route {...props}>
      {children}
    </Route>
  );

const TIMEOUT = 700;
const APP_ROUTE_HTML = 'html';
const APP_ROUTE_WEBGL = 'webgl';
const APP_ROUTE_PDF = 'pdf';

const APP_ROUTES = [APP_ROUTE_HTML, APP_ROUTE_WEBGL, APP_ROUTE_PDF];

type IMPLEMENTATION_MAP = { [key: string]: () => ReactElement };
const IMPLEMENTATION_COMPONENTS : IMPLEMENTATION_MAP = {
  [APP_ROUTE_HTML]: HtmlApp,
  [APP_ROUTE_WEBGL]: WebglApp,
  [APP_ROUTE_PDF]: PdfApp
};

export function CvApp({ browserCapability, location }: { browserCapability?: Boolean, location?: Location }) {
  const defaultApp = APP_ROUTE_PDF;//useMemo(() => browserCapability ? APP_ROUTE_WEBGL : APP_ROUTE_HTML, [browserCapability]);
  const { params: { appImplementation } } = useRouteMatch();

  const renderImplementation = useMemo(() => {
    const supportedImplementations = uniq([APP_ROUTE_HTML, APP_ROUTE_PDF, defaultApp, APP_ROUTE_WEBGL]);
    return supportedImplementations.includes(appImplementation)
      ? appImplementation
      : defaultApp;
    }, [defaultApp, appImplementation]);

  const ImplementationComponent = IMPLEMENTATION_COMPONENTS[renderImplementation];

  const defaultAppRoute = useAppImplementationLocation(defaultApp) as LocationDescriptor;

  const isApp = useMemo(() => APP_ROUTES.includes(appImplementation), [appImplementation]);

  const appRender = useCallback((status) => (
    <AppImplementation
      status={status}
      implementation={renderImplementation}
      timeout={TIMEOUT}
    >
      <Switch location={location}>
        <ApplicationProvider path="/webgl">
          <ImplementationComponent />
          {
            !browserCapability && <CapabilityWarning defaultApp={defaultApp} />
          }
        </ApplicationProvider>
        <ApplicationProvider path="/:implementation">
          <ImplementationComponent />
        </ApplicationProvider>
      </Switch>
    </AppImplementation>
  ), [browserCapability, defaultApp, location, renderImplementation])
  return isApp
        ? (
          <>
            <TransitionGroup className="cv-app">
              <Transition
                key={appImplementation}
                timeout={TIMEOUT}
                appear
              >
                {appRender}
              </Transition>
            </TransitionGroup>
            {/* <Toolbar /> */}
          </>
        )
        : (
          <Redirect
            to={defaultAppRoute}
          />
        )
}



