const STATUS_PENDING = 'pending';
const STATUS_SUCCESS = 'success';
const STATUS_ERROR = 'error';

type PromiseStatusType = typeof STATUS_PENDING | typeof STATUS_SUCCESS | typeof STATUS_ERROR;

export class PromiseWrapper {
    private status: PromiseStatusType;
    private response?: any;
    private suspender: Promise<any>;

    constructor(promise: Promise<any>) {
        this.status = STATUS_PENDING;

        this.suspender = promise
            .then(
                (response) => {
                    this.status = STATUS_SUCCESS;
                    this.response = response;
                },
                (error) => {
                    this.status = STATUS_ERROR;
                    this.response = error;
                }
            );
    }

    static from(source: Promise<any> | PromiseWrapper) {
        if (source instanceof PromiseWrapper) {
            return source;
        }

        return new PromiseWrapper(source);
    }

    read() {
        const {status, suspender, response} = this;
        switch (status) {
            case STATUS_PENDING:
                throw suspender;
            case STATUS_ERROR:
                throw response;
            default:
                return response;
        }
    }
}
