import React, { useMemo, ReactChildren, memo } from 'react';
import { flatten, map, compact } from 'lodash';

import {Link} from '../../../Link';

const IS_LINK = /(\([^)]*?\|.*?\))/;
const LINK_PARSE = /\(([^)]*?)\|(.*?)\)/;

const ExperiencePositionDescriptionFragment = memo(({children}: {children: string | ReactChildren}) => {
    const linkMatch = LINK_PARSE.exec(children as string);

    if (linkMatch) {

        const [, text, href] = linkMatch;

        return (
            <Link
                to={href || ''}
            >
                {text}
            </Link>
        )
    }

    return <>{children}</>;
});

export function ExperiencePositionDescription({description}: {description: string | string[]}) {
    const content = useMemo(() => map(
        compact(
            flatten(
                [description]
            )
        ),
        line => [
            line.split(IS_LINK)
                .map(fragment => (
                    <ExperiencePositionDescriptionFragment
                        key={fragment}
                    >
                        {fragment}
                    </ExperiencePositionDescriptionFragment>
                ))
        ]
    ).reduce((lines: any[], line) => [...lines, <br key={`oel-${lines.length}`} />, line]), [description])
    return (
        <p className="cv-pdf-experience-position-description">
            {content}
        </p>
    )
}