const WEB_GL_CONTEXTS = [
    'webgl',
    'experimental-webgl',
    // 'moz-webgl',
    // 'webkit-3d'
];

export function getWebglContext(canvas) {
    if (canvas) {
        return WEB_GL_CONTEXTS
            .reduce(
                (canvasContext, contextName) => canvasContext || canvas.getContext(contextName),
                false
            ) || null;
    }
    return null;
}