import React from 'react';
import { useThree } from 'react-three-fiber';
import { AppImplementationContext } from '../AppImplementation';
import { ReactChildren, useMemo } from 'react';

export function AppClock({appImplementation, children}: {appImplementation: {
    timeout: number
}, children: ReactChildren}) {
    const {clock: {elapsedTime}} = useThree();

    const appImplementationClock = useMemo(() => ({
        ...appImplementation,
        startClock: elapsedTime,
        endClock: elapsedTime + appImplementation.timeout / 1000,
        timeoutClock: appImplementation.timeout / 1000
    }), [appImplementation, elapsedTime]);

    return (
        <AppImplementationContext.Provider value={appImplementationClock}>
            {children}
        </AppImplementationContext.Provider>
    );
}