export function svgAngle(ux: number, uy: number, vx: number, vy: number) {
    const dot = ux * vx + uy * vy;
    const len = Math.sqrt(ux * ux + uy * uy) * Math.sqrt(vx * vx + vy * vy);
    const ang = Math.acos(Math.max(-1, Math.min(1, dot / len))); // floating point precision, slightly over values appear

    const sign = Math.sign(ux * vy - uy * vx);

    return ang * sign;
}
