import React, { useMemo } from 'react';
import {map, get} from 'lodash';

import {PositionDescriptor, useExperience} from '../../../../hooks';

import {Link} from '../../../Link';
import {TechnologyTag} from '../../TechnologyTag';

import {ExperiencePositionDescription} from './ExperiencePositionDescription';
import {ExperiencePositionHead} from './ExperiencePositionHead';
import { Row, Cell } from '../../Document';

const EXPERIENCE_SHARE = 1.5 * 4.5;

export function ExperiencePosition({
    position,
    rangeLength,
    prevPosition,
    nextPosition
}: {
    position: PositionDescriptor,
    rangeLength: number,
    prevPosition?: PositionDescriptor
    nextPosition?: PositionDescriptor
}) {
    const {minLength, lengthDistribution} = useExperience();
    const {
        positionLength,
        marginBottom
    } = useMemo(() => {
        const start = get(prevPosition, 'end', position.start);
        const duration = position.end.diff(start, 'days');
        const offset = position.start.diff(start, 'days');

        const positionPart = Math.log((duration - offset) / (rangeLength | 1) * 250) * EXPERIENCE_SHARE;
        const positionOffset = Math.max(.07, Math.log(offset / (rangeLength | 1) * 100)) * EXPERIENCE_SHARE;
        const positionLength = (duration - minLength) / lengthDistribution;

        const marginBottom = `${positionOffset}vh`;
        return {
            marginBottom,
            positionLength
        };
    }, [position, prevPosition, rangeLength]);

    return (
        <Row className="cv-pdf-experience-position">
            <Cell>
                <article className="cv-pdf-experience-position-client" style={{marginBottom}}>
                    <ExperiencePositionHead
                        position={position}
                        nextPosition={nextPosition}
                        positionLength={positionLength}
                    />
                    <div className="cv-pdf-experience-position-content">
                        <p className="cv-pdf-experience-position-context">
                            <strong
                                className="cv-pdf-experience-position-company"
                            >
                                {
                                    position.linkCompany
                                        ? (
                                            <Link
                                                className="cv-pdf-experience-position-company-link"
                                                to={position.linkCompany}
                                            >
                                                {position.company}
                                            </Link>
                                        )
                                        : position.company
                                }
                            </strong>, {position.city}
                            <br />
                            <span className="cv-pdf-experience-position-company-product">
                                {position.companyProduct}
                            </span>
                        </p>
                        <ExperiencePositionDescription
                            description={position.description}
                        />
                        <p className="cv-pdf-experience-position-technologies">
                            {
                                map(
                                    position.technologies,
                                    technology => (
                                        <TechnologyTag
                                            key={technology}
                                            className="cv-pdf-experience-position-technology"
                                        >
                                            {technology}
                                        </TechnologyTag>
                                    )
                                )
                            }
                        </p>
                    </div>
                </article>
            </Cell>
        </Row>
    );
}
