import {useMemo} from 'react';
import {find} from 'lodash';

export function useCurrentPosition(match = {}, experienceTimeline) {
    const {
        params: {positionId: id} = {}
    } = match;

    const currentPosition = useMemo(() => (
        id
            ? (find(experienceTimeline, {id}) || false)
            : undefined
    ), [id, experienceTimeline]);

    return useMemo(() => ({
        id,
        currentPosition
    }), [currentPosition, id]);
}