import React, { useState, useCallback, useMemo, useLayoutEffect } from 'react';

import { Tool } from './Tools';
import { useImplementation } from '../AppImplementation';
import { useSpring, animated as a } from '@react-spring/three';
import { useHover } from 'react-use-gesture';

const scaleConfigs = {
    0: {mass: 1.5, friction: 70, tension: 1000},
    1: {mass: 3, friction: 40, tension: 800}
};

export function Home() {
    const [frustum, setFrustum] = useState(true);
    const [hovered, setHovered] = useState(false);
    // const [hovers, hovered] = useHover();
    // const [hovers2, hovered2] = useHover();
    const color2 = 0x00ff00;//useMemo(() => hovered2 ? 0x0000ff : 0x00ff00, [hovered2]);

    const frustumOn = useCallback(() => setFrustum(true), []);
    const frustumOff = useCallback((e) => {
        e.stopPropagation();
        setFrustum(false);
    }, []);
    const color = useMemo(() => hovered ? 0x0000ff : 0x00ff00, [hovered]);

    const {status} = useImplementation();

    const scale = useMemo(() => Number(['entering', 'entered'].includes(status)), [status]);
    const [spring, set] = useSpring(() => ({
        scale: [0, 0, 0],
        config: scaleConfigs[0]
    }));

    const bindHover = useHover(({ hovering }) => setHovered(hovering), {
        pointerEvents: true
    });

    useLayoutEffect(() => void set({scale: [scale, scale, scale], config: scaleConfigs[scale]}), [scale, set]);

    return (
        <>
            <a.mesh {...spring} position={[.5,0,0]} onClick={frustumOn} {...bindHover()}>
                <sphereGeometry attach="geometry" args={[3, 30, 10]} />
                <a.meshBasicMaterial attach="material" color={color} wireframe />
            </a.mesh>
            <Tool>
                {
                    ({dimensions}) => (
                        <mesh
                            visible={frustum}
                            onClick={frustumOff}
                            // {...hovers2}
                        >
                            <planeGeometry attach="geometry" args={dimensions} />
                            <meshBasicMaterial attach="material" color={color2} wireframe />
                        </mesh>
                    )
                }
            </Tool>
        </>
    );
}