import React, { Suspense } from 'react';
import { Tool } from './Tools';
import { Text } from './Text';
import { HALF_PI } from '../../constants';
import { Color } from 'three';
import { useSpring, a } from '@react-spring/three';

const initialRotation = (window.innerWidth > window.innerHeight) ? 0 : -HALF_PI;

const Name = ({landscape, clipBoxW, clipBoxH}: {landscape: boolean, clipBoxW: number, clipBoxH: number}) => {

    const size = landscape ? clipBoxW : clipBoxH;
    const fontSize = 14 * size / .3;

    const shiftH = -200 * (clipBoxW || .1) / .1;
    const shiftV = 200 * (clipBoxH || .1) / .1;

    const {rotation, position} = useSpring({
        from: {rotation: initialRotation},
        rotation: landscape ?  0 : -HALF_PI,
        position: (landscape ? [ 0, shiftV, -700 ] : [ shiftH, 0, -700 ]) as [number, number, number]
    });
    return (
        <a.group position={position} scale={[50, 50,  1]} rotation={rotation.to(r => [0, 0, r])}>
            <Suspense fallback={
                <group />
            }>
                <Text
                    fontName="/fonts/micro-extend/MicroExtendFLF.ttf"
                    color="#373757"
                    position={[0,  0, 0]}
                    size={fontSize}
                    hAlign="right"
                >
                    Artem
                </Text>
                <Text
                    fontName="/fonts/micro-extend/MicroExtendFLF-BoldItalic.ttf"
                    position={[0,  0, 0]}
                    size={fontSize}
                >
                    {' Kudla'}
                    <meshStandardMaterial
                        attach="material"
                        color="#373737" //#524e42
                        roughness={1}
                        metalness={.1}
                        emissive={new Color(0xffee1f)}
                        emissiveIntensity={.01}
                    />
                </Text>
            </Suspense>
        </a.group>
    );
}
export function WebglLogo() {
    return (
        <Tool>
            {
                ({landscape, clipBoxW, clipBoxH}) => (
                    <Name
                        landscape={landscape}
                        clipBoxW={clipBoxW}
                        clipBoxH={clipBoxH}
                    />
                )
            }
    </Tool>

    )
}