import React, { useState, useCallback, useEffect } from 'react';
import { TransitionGroup, Transition } from 'react-transition-group';

import { useExperience } from '../../../hooks';

import fontNames from './fonts.json';

import { ExperienceTimeline } from './ExperienceTimeline';
import { ExperiencePosition } from './ExperiencePosition';

export function Experience() {
    const { experience, currentPosition } = useExperience();

    const [fontName, setFontName] = useState(fontNames[0]);
    const [ccw, setCcw] = useState(false);

    const nextFont = useCallback(
        inc => setFontName(fontNames[(fontNames.indexOf(fontName) + fontNames.length + inc) % fontNames.length]),
        [fontName, setFontName]
    );

    // const onFontClick = useCallback(({shiftKey, altKey}) => {
    //     if (altKey) {
    //         return setCcw(!ccw);
    //     }
    //     nextFont(1 - 2 * shiftKey);
    // }, [nextFont, ccw]);

    useEffect(
        () => {
            const onKeyDown = ({code}) => {
                if (code === 'KeyC') {
                    setCcw(!ccw)
                }
                if (['KeyA', 'KeyD'].includes(code)) {
                    nextFont(1 - 2 * (code === 'KeyA'));
                }
            }
            window.addEventListener('keydown', onKeyDown);
            return () => {
                window.removeEventListener('keydown', onKeyDown);
            }
        },
        [ccw, setCcw, nextFont]
    )


    return (
        <>
            <ExperienceTimeline experience={experience} />
            {/* <Text color="red" fontName={fontName} ccw={ccw} onClick={onFontClick} position={[-30, 20, 0]}>{fontName}</Text> */}
            <TransitionGroup component="group">
                {
                    currentPosition
                    && (
                        <Transition
                            // key={currentPosition.id}
                            timeout={700}
                            appear
                        >
                            {state => (
                                <ExperiencePosition
                                    fontName={fontName}
                                    ccw={ccw}
                                    experiencePosition={currentPosition}
                                    position={[0,0,-25]}
                                    // rotation={[0, Math.PI, 0]}
                                    state={state}
                                />
                            )}
                        </Transition>
                    )
                }
            </TransitionGroup>
        </>
    );
}