import { useLocation } from "react-router";
import { useMemo } from "react";

export function useAppImplementationLocation(implementation? : string) {
    const location = useLocation();
    return useMemo(() => {
        const { pathname = ''} = location;

        if (implementation) {
            return {
                ...location,
                pathname: pathname.replace(/^((\/(html|webgl|pdf)(?=\/|$))|\/$)?/, `/${implementation}`)
            };
        }
    }, [location, implementation])
}
