import React, { useMemo } from 'react';
import moment from 'moment';

import { PositionDescriptor, useClassNames } from "../../../../../hooks";

const now = moment.utc();

const pureColor = [0xee, 0x4d, 0x64];

export function ExperiencePositionHead({
    position,
    nextPosition,
    // positionLength
}: {
        position: PositionDescriptor,
        nextPosition?: PositionDescriptor,
        positionLength: number
}) {

    const hasSuccessor = useMemo(
        () => nextPosition
            && position.end.clone().add(3, 'week').isSameOrAfter(nextPosition.start),
        [position, nextPosition]);

    const  endUnix = position.end.unix();

    const endTitle = useMemo(
    () => now.diff(endUnix && position.end, 'weeks') > 3
            ? position.end.format('MMM YYYY')
            : 'Present',
        [endUnix, position.end]
    );

    // const color = useMemo(
    //     () => pureColor
    //         .map(value => Math.min(255, Math.round(value + 45 * (1 - positionLength))))
    //         .map(value => value.toString(16))
    //         .join(''),
    //     [positionLength]
    // )

    // const minHeight = useMemo(() => `${positionPart}vh`, [positionPart]);

    return (
        <>
            <h3 className="cv-pdf-experience-position-title">{position.name}</h3>

            <h4
                className="cv-pdf-experience-position-timeline"
                style={{
                    // backgroundColor: `#${color}`
                }}
            >
                <div
                    className="cv-pdf-experience-position-part"
                    // style={{minHeight}}
                />
                <span className="cv-pdf-experience-position-range cv-pdf-experience-position-range-start">
                    {position.start.format('MMM YYYY')}
                </span>
                <span className={useClassNames(
                    [hasSuccessor],
                    'cv-pdf-experience-position-range',
                    'cv-pdf-experience-position-range-end',
                    {
                        'cv-pdf-experience-position-range-terminated': hasSuccessor
                    }
                )}>
                    {endTitle}
                </span>
                <span className="cv-pdf-experience-position-duration">
                    {position.end.from(position.start, true)}
                </span>
            </h4>

        </>
    )
}