import {useMemo, ReactNode} from "react";
import {createPortal} from "react-three-fiber";

import {useTools, ToolsContextType} from "./ToolsContext";

export type ToolChild = ReactNode | ((context: ToolsContextType) => ReactNode);

export function Tool({children}: {children: ToolChild}) {
    const toolsContext = useTools();

    const renderedChildren = useMemo(() => children instanceof Function
        ? children(toolsContext)
        : children
    , [children, toolsContext]);

    const {group, viewPortInitiated} = toolsContext;

    return (group && viewPortInitiated)
        ? createPortal(renderedChildren, group) as unknown as JSX.Element
        : null;
}