import React, { ReactNode } from 'react';
import { useSection } from '../useSection';
import { createPortal } from 'react-dom';

export const Footer = ({children}: {children?: ReactNode}) => {
    const {
        footerPlaceholder,
        footerStatic
    } = useSection();
    return (
        <>
            {
                footerPlaceholder
                    ? createPortal(children, footerPlaceholder)
                    :  null
            }
            {
                footerStatic
                    ? createPortal(children, footerStatic)
                    :  null
            }
        </>
    );
};
