import React, { Suspense } from 'react';
import {HashRouter as Router, Switch, Route, useRouteMatch, useLocation} from 'react-router-dom';
import {Canvas} from 'react-three-fiber';
import {BackSide} from 'three';

import './resources';

import {Home} from './Home';
import {Experience} from './Experience';
import {ToolsFrustum, Tool} from './Tools';
import {ViewControl} from './ViewControl';
import { useImplementation } from '../AppImplementation';
import { AppClock } from './AppClock';
import { WebglLogo } from './WebglLogo';

export function WebglApp() {
    const {path} = useRouteMatch();
    const location = useLocation();
    const appImplementation = useImplementation();

    return (
        <Canvas
            className="cv-webgl-context"
            camera={{
                fov: 80,
                position: [0, 0, 100],
                near: 0.1,
                far: 20000
              }}
        >
            <mesh>
                <sphereGeometry attach="geometry" args={[1500, 15, 15]}/>
                <meshStandardMaterial
                    attach="material"
                    side={BackSide}
                    roughness={1}
                    metalness={.1}
                    color="#333"
                />
            </mesh>
            <AppClock appImplementation={appImplementation}>
                <ToolsFrustum>
                    <Tool>
                        <ambientLight intensity={.2} color={0xffaaff} />
                        <spotLight intensity={.7} position={[300, 100, 700000]} />
                    </Tool>
                    <WebglLogo />

                    <ViewControl />
                    <Router>
                        <Switch location={location}>
                            <Route exact path={`${path}/`}>
                                <Home />
                            </Route>
                            <Route path={`${path}/experience/:positionId`}>
                                experience
                                <Suspense fallback={null}>
                                    <Experience />
                                </Suspense>
                            </Route>
                            <Route path={`${path}/experience`}>
                                experience position
                                <Experience />
                            </Route>
                        </Switch>
                    </Router>
                </ToolsFrustum>
            </AppClock>
        </Canvas>
    );
}