import {useMemo} from 'react';
import {useLocation, useRouteMatch} from 'react-router-dom';

export function usePositionLocation(position) {
    const {id: positionId} = position || {};
    const location = useLocation();
    const { path, params } = useRouteMatch();

    return useMemo(() => ({
        ...location,
        pathname: path
            .replace(/:implementation/, params.implementation)
            .replace(/(\/:positionId)?$/, position ? `/${positionId}` : '')
    }), [position, positionId, location, path, params]);
}