import { parse } from 'opentype.js';
import { useAxios } from './useAxios';
import { useMemo } from 'react';
import TextToSVG from '@spurtli/text-to-svg';


export const useFont = (fontName: string) => {
    const {data: fontSource} = useAxios({
        // url: `/fonts/${fontName}`,
        url: `${fontName}`,
        responseType: 'arraybuffer'
    });
    return useMemo(() => new TextToSVG(parse(fontSource)) as TextToSVG.TextToSVG, [fontSource]);
}